@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
	font-family: 'Open Sans', sans-serif;
	color: #00309f;
}

.header-container {
	text-align: center;
	color: #00309f;
	margin-bottom: 30px;
}

.header-title {
	margin-bottom: 0;
	margin-top: 50px;
	font-size: 60px;
}

.header-subtitle {
	margin-top: -12px;
	font-size: 22px;
}

.header-link {
	text-decoration: none;
	color: #00309f;
	display: block;
	width: -webkit-fit-content;
	width: -moz-fit-content;
	width: fit-content;
	margin: 0 auto;
}

.card {
	border: 4px solid #00309f;
	border-radius: 12px;
}

.platform-card {
	margin-bottom: 20px;
	width: 400px;
	margin-left: auto;
	margin-right: auto;
}

.platform-title {
	text-align: center;
	color: #00309f;
	font-size: 15px;
}

.platform-link-container {
	display: flex;
	justify-content: center;
	margin-bottom: 40px;
}

.platform-link {
	margin-left: 5px;
	margin-right: 5px;
	text-decoration: none;
	color: #00309f;
	font-weight: bold;
	border: 2.5px solid #00309f;
	padding: 7px 0;
	width: 150px;
	border-radius: 12px;
	display: flex;
	align-items: flex-start;
	justify-content: center;
}

.platform-link:hover {
	background-color: rgb(231, 236, 253);
}

.platform-link img {
	margin: 0;
	width: 30px;
}

.platform-link p {
	margin: 0;
	margin-top: 3px;
	margin-left: 5px;
}

.home-flex-container {
	display: flex;
	justify-content: center;
}

.home-flex-container .card {
	width: 450px;
}

.home-flex-column {
	margin-left: 20px;
	margin-right: 20px;
	text-align: center;
}

.card-title {
	font-size: 20px;
	margin-bottom: 15px;
}

.card-title span {
	font-weight: normal;
}

.saved-card {
	margin-bottom: 10px;
}

.about-card {
	text-align: left;
	padding: 20px 30px;
}

.about-card-title {
	font-weight: bold;
	margin-bottom: 10px;
	margin-top: 0;
}

.about-card-description {
	margin-top: 0;
}

.about-card-description span {
	font-weight: bold;
}

.btn {
	background: none;
	background-color: none;
	outline: none;
	border: 2.5px solid #00309f;
	color: #00309f;
	font-weight: bold;
	padding: 6px 30px;
	border-radius: 13px;
	cursor: pointer;
}

.btn:hover {
	background-color: rgb(231, 236, 253);
}

.about-card-btn {
	margin-right: 10px;
}

.saved-card {
	text-align: left;
	display: flex;
	align-items: flex-start;
	padding: 20px;
}

.saved-card-title {
	font-size: 14px;
	margin-bottom: 0;
	margin-top: 0;
}

.saved-card-url {
	font-size: 14px;
	margin-top: 0;
}

.saved-card-contents {
	margin-left: 15px;
}

.no-saved-message {
	display: block;
	width: 450px;
}

.add-card {
	margin-bottom: 20px;
	width: 350px;
	margin-left: auto;
	margin-right: auto;
	padding: 20px;
}

.add-card-heading img {
	width: 50px;
}

.add-card-heading {
	display: flex;
	align-items: flex-start;
	margin-bottom: 15px;
}

.add-card-heading-text h3 {
	margin-top: 3px;
	margin-bottom: 0;
}

.add-card-heading-text p {
	margin-top: -3px;
	font-size: 13px;
}

.add-card-heading-text {
	margin-left: 10px;
}

.add-card-body input {
	display: block;
	margin: 0 auto;
	margin-bottom: 5px;
	padding: 5px 10px;
	text-align: center;
	width: 300px;
	font-size: 15px;
	color: #00309f;
	outline: none;
	border: 2.5px solid #00309f;
}

::-webkit-input-placeholder {
	color: #00309f70;
	opacity: 1;
}

:-ms-input-placeholder {
	color: #00309f70;
	opacity: 1;
}

::-ms-input-placeholder {
	color: #00309f70;
	opacity: 1;
}

::placeholder {
	color: #00309f70;
	opacity: 1;
}

:-ms-input-placeholder {
	color: #00309f70;
}

::-ms-input-placeholder {
	color: #00309f70;
}

.add-card-btn-container {
	display: flex;
	justify-content: center;
	margin-top: 25px;
}

.hidden-link {
	text-decoration: none;
	color: #00309f;
}

.webhook-details-text {
	font-size: 13px;
	margin-top: 0;
	margin-bottom: 0;
	text-align: left;
	margin-left: 10px;
}

.message-text-area {
	resize: none;
	border: 2.5px solid #00309f;
	font-family: 'Open Sans', sans-serif;
	color: #00309f;
	width: 250px;
	height: 50px;
	margin-bottom: -15px;
	padding: 5px 10px;
	outline: none;
}

.body-section {
	padding-bottom: 50px;
	min-height: calc(100vh - 300px);
}

.footer-section {
	text-align: center;
	height: 50px;
}

.mode-switcher-link {
	color: #00309f;
	font-size: 13px;
}

.mode-switcher-link-selected {
	text-decoration: none;
	cursor: unset;
}

.err-message {
	color: red;
	font-weight: bold;
	text-align: center;
}

.success-message {
	color: green;
	font-weight: bold;
	text-align: center;
}

:focus {
	outline: none;
}
::-moz-focus-inner {
	border: 0;
}

@media only screen and (max-width: 1060px) {
	.home-flex-container {
		flex-direction: column;
		align-items: center;
	}
}

@media only screen and (max-width: 600px) {
	.about-card-column {
		display: none !important;
	}
}

@-webkit-keyframes fadeup {
	from {
		opacity: 0;
		transform: translateY(30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@keyframes fadeup {
	from {
		opacity: 0;
		transform: translateY(30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@-webkit-keyframes fadeleft {
	from {
		opacity: 0;
		transform: translateX(30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@keyframes fadeleft {
	from {
		opacity: 0;
		transform: translateX(30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@-webkit-keyframes faderight {
	from {
		opacity: 0;
		transform: translateX(-30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@keyframes faderight {
	from {
		opacity: 0;
		transform: translateX(-30px);
	}
	to {
		opacity: 1;
		transform: none;
	}
}

@-webkit-keyframes zoomfadeout {
	from {
		opacity: 1;
		transform: scale(1);
	}
	to {
		opacity: 1;
		padding: 0;
		margin: 0;
		transform: scale(0);
		height: 0;
	}
}

@keyframes zoomfadeout {
	from {
		opacity: 1;
		transform: scale(1);
	}
	to {
		opacity: 1;
		padding: 0;
		margin: 0;
		transform: scale(0);
		height: 0;
	}
}

.platform-card {
	opacity: 0;
	-webkit-animation-name: fadeup;
	        animation-name: fadeup;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.home-delay-ani {
	opacity: 0;
	-webkit-animation-name: fadeup;
	        animation-name: fadeup;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-delay: .5s;
	        animation-delay: .5s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.add-edit-ani {
	opacity: 0;
	-webkit-animation-name: fadeup;
	        animation-name: fadeup;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.left-card-ani {
	opacity: 0;
	-webkit-animation-name: fadeleft;
	        animation-name: fadeleft;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.right-card-ani {
	opacity: 0;
	-webkit-animation-name: faderight;
	        animation-name: faderight;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

.removed-card {
	opacity: 0;
	-webkit-animation-name: zoomfadeout;
	        animation-name: zoomfadeout;
	-webkit-animation-duration: .5s;
	        animation-duration: .5s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
}

